@import "config"
@import "typography"

*, html, body
  font-family: $font-stack
  font-size: $small-text
  margin: 0
  padding: 0
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  box-sizing: border-box
  outline: none
  cursor: default

body::-webkit-scrollbar
  display: none

#root > .app
  display: flex
  flex-direction: column
  padding: 3%
  background-color: $background-color

header
  width: 100%
  font-weight: bolder
  user-select: none

nav
  text-align: right
  display: flex
  flex-direction: column
  justify-content: flex-end
  align-items: flex-end
  flex-grow: 1

main
  display: flex
  flex-direction: column
  justify-content: center
  max-height: 100%
  max-width: 100%
  flex-grow: 1
  padding-top: 1vmin
  padding-bottom: 1vmin
  overflow: scroll
  -ms-overflow-style: none
  scrollbar-width: none

main::-webkit-scrollbar
  display: none

main.start
  justify-content: start

main.center
  align-items: center

  &.shrink
    //max-height: 100%
    //overflow: hidden


footer
  display: flex
  justify-content: space-between
  align-items: center
