%textformat
  font-size: $small-text
  color: $primary-color
  text-decoration: none

*, html, body
  @extend %textformat

h1, .letter
  font-size: $big-text


h2, label, select, p.highlighted, input, button
  font-size: $middle-text
  font-weight: bolder

h3
  font-size: $small-text
  font-weight: bolder


select, option, input, .highlighted
  color: $select-color

a.big
  font-size: $big-text
  text-transform: uppercase
  font-weight: bolder

a.title
  text-transform: lowercase
  font-weight: bold

a:link, a:visited
  color: $primary-color

a:hover, a:active
  color: $primary-color
  text-decoration-style: solid
  text-decoration-color: $primary-color
  text-decoration-line: underline
  text-decoration-thickness: $border-rad

.material-symbols-outlined
  font-size: $middle-text
  font-variation-settings: 'FILL' 1, 'wght' 1000, 'GRAD' 0, 'opsz' 40
