.clockgrid
  display: grid
  grid-template-columns: repeat(24, 1fr)
  gap: 5px

  .clock
    aspect-ratio: 1 / 1
    width: 60px
    height: 60px
    position: relative
    display: flex
    flex-direction: column
    box-shadow: rgba(50, 50, 93, 0.25) 0px 3px 3px -2px inset, rgba(201, 201, 201, 0.3) 1px 2px 3px 3px inset
    border-radius: 50%

    p
      font-size: 18px

    .hour,
    .minute
      width: 50%
      right: 50%
      height: 3px
      position: absolute
      background: black
      top: calc(50% - 1.5px)
      transform-origin: 100%
      //transition-timing-function: steps(60, jump-start)
      transition-timing-function: ease-out
      //cubic-bezier(0.1, 2.7, 0.58, 1)
      transition-duration: 8s
      transition-property: transform

    .middle
      background: black
      position: absolute
      width: 3px
      height: 3px
      top: calc(50% - 1.5px)
      left: calc(50% - 1.5px)
      z-index: 3
      border-radius: 50%
