@import "config"

.game
    display: flex
    flex-direction: column

.word
    display: flex

.input-field-hidden
    position: absolute
    opacity: 0
    max-width: 10px
    cursor: pointer

.letter
    border: $border-rad solid $primary-color
    display: flex
    align-items: center
    justify-content: center
    width: 8vmin
    height: 8vmin
    font-weight: bold
    text-align: center
    color: $primary-color
    text-transform: uppercase
    margin: 0.05rem
    cursor: pointer

.letter.correct
    background-color: rgba(201, 252, 101, 0.92)

.letter.position
    background-color: rgba(254, 225, 76, 0.9)

.letter.wrong
    background-color: #f1f1f1

.letter.disabled
    background-color: #3d3d3d

.reveal-solution
    text-align: center

.shake
  animation: shake 0.2s

@keyframes shake
    0%
        transform: translate(1px, 1px) rotate(0deg)
    10%
        transform: translate(-1px, -2px) rotate(-1deg)
    20%
        transform: translate(-3px, 0px) rotate(1deg)
    30%
        transform: translate(3px, 2px) rotate(0deg)
    40%
        transform: translate(1px, -1px) rotate(1deg)
    50%
        transform: translate(-1px, 2px) rotate(-1deg)
    60%
        transform: translate(-3px, 1px) rotate(0deg)
    70%
        transform: translate(3px, 1px) rotate(-1deg)
    80%
        transform: translate(-1px, -1px) rotate(1deg)
    90%
        transform: translate(1px, 2px) rotate(0deg)
    100%
        transform: translate(1px, -2px) rotate(-1deg)
