@import "config"

input
  border-style: none
  background-color: transparent
  text-align: left
  -webkit-appearance: none

select
  border: 0
  margin: 0
  border-radius: $border-rad
  padding-right: 25px
  background-color: transparent
  -webkit-box-sizing: border-box
  -moz-box-sizing: border-box
  box-sizing: border-box
  -webkit-appearance: none
  -moz-appearance: none

select, input
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23" + "9d9d9d" + "' height='48' width='48'%3e%3cpath d='m24 30.75-12-12 2.15-2.15L24 26.5l9.85-9.85L36 18.8Z'/%3e%3c/svg%3e")
  background-repeat: no-repeat
  background-position: right
  background-size: 1.5em auto, 100%

input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-calendar-picker-indicator
    display: none
    -webkit-appearance: none

input::-webkit-date-and-time-value
    text-align: left


select.round
  background-image: linear-gradient(45deg, transparent 50%, gray 50%), linear-gradient(135deg, gray 50%, transparent 50%), radial-gradient(#ddd 70%, transparent 72%)
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), calc(100% - .5em) .5em
  background-size: 5px 5px, 5px 5px, 1.5em 1.5em
  background-repeat: no-repeat

select.round:focus
  background-image: linear-gradient(45deg, black 50%, transparent 50%), linear-gradient(135deg, transparent 50%, white 50%), radial-gradient(gray 70%, transparent 72%)
  background-position: calc(100% - 15px) 1em, calc(100% - 20px) 1em, calc(100% - .5em) .5em
  background-size: 5px 5px, 5px 5px, 1.5em 1.5em
  background-repeat: no-repeat
  border-color: transparent
  outline: 0



.alert-message
  color: $highlight-color

button
  border: none
  background: transparent

a, button, button > *, .switch, select, input
  cursor: pointer
