@import "config"

form
  display: grid
  grid-template-columns: 1fr 1.2fr
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr
  column-gap: 4%

form > label
  text-align: right

.training-list
  height: 100%
  width: 100%
  display: flex
  flex-direction: column
  gap: 1%
  overflow: scroll

.training-list::-webkit-scrollbar
  display: none

.list-session
  border: $border-rad solid $primary-color
  width: 100%
  display: flex
  justify-content: space-between
  align-items: center
  min-height: 130px
  gap: 1%
  cursor: pointer

.list-image
  height: 100%

.list-date
  font-size: 2rem
  width: 15%
  text-align: center
  font-weight: bolder

.list-duration
  padding-left: 2%
  color: $select-color

.list-title
  flex-grow: 2

.list-name
  font-weight: bolder

rect
  fill: $select-color

.list-ranges
  transform: scaleY(-1)
  width: 40%
  height: 90%
  padding-right: 1%

@media only screen and (max-width: 1026px)
    .list-ranges
        display: none
