@import "config"

.modal-background
  position: fixed
  top: 0
  left: 0
  height: 100%
  width: 100%

.modal-wrapper
  border: $border-rad solid $primary-color
  background: $background-color
  position: fixed
  top: 50%
  left: 50%
  box-sizing: border-box
  transform: translate(-50%, -50%)
  min-width: 60%
  min-height: 60%
  max-width: 80%
  max-height: 80%

  .modal
    position: inherit
    display: flex
    flex-direction: column
    max-height: 100%
    width: 100%
    padding: 3%

    .first
      display: flex
      padding-bottom: 3%
      justify-content: space-between

    .modal-main
      width: 100%
      overflow: scroll
      float: left

    h3
      padding-top: 3%

    .modal-main::-webkit-scrollbar
      display: none

.settings-row
  display: flex
  flex-direction: row
  justify-content: space-between

